export default ContactSocials;

const Container = styled.div`
  width: 100vw;
  height: 100vh;
  background-color: #151515;
  overflow: hidden;
  p{
    font-family: Quicksand;
font-style: normal;
font-weight: normal;
font-size: 100px;
line-height: 125px;
text-align: center;
letter-spacing: 0.345em;

color: #F5F5F5;
  }
    /* screen width is less than 450px (small) */
  ${media.lessThan("small")`
padding-top:10vh;
height: 150vh;
  `}
    /* screen width is between 450px and 768px (small to medium) */
  ${media.between("small", "medium")`
  padding-top:10vh;
height: 100vh;
  `}

`;

const SocialGrid = styled.div`
  width: 60%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-content: center;
  .icon {
    margin: 30px 0;
  }
  p {
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 50px;
    line-height: 75px;
    letter-spacing: 0.05em;

    color: #ffffff;
  }
  ${media.lessThan("small")`
    
    grid-gap: 35px;
    .icon {
    margin: 0px 0;
    width:30%;
  }
p{
  font-size: 24px;
    line-height: 35px;
}
  `}
    /* screen width is between 450px and 768px (small to medium) */
  ${media.between("small", "medium")`
    grid-gap: 35px;
    margin-top:10vh;
    .icon {
    margin: 0px 0;
    width:30%;
  }
 p{
   font-size: 24px;
    line-height: 35px;
  }
  `}
`;
